.wprf-toggle-wrap {
  padding-left: 55px;
  margin: 15px 0 25px;
}
.wprf-toggle-wrap.checked label:before {
  background: #2df16e;
}
.wprf-toggle-wrap.checked label:after {
  left: -13px;
}
.wprf-toggle-wrap label {
  line-height: 30px;
  position: relative;
  min-height: 30px;
  padding-left: 25px;
  margin-bottom: 0;
  font-weight: 400;
  cursor: pointer;
  display: inline-block;
}
.wprf-toggle-wrap label input[type=checkbox] {
  display: none;
}
.wprf-toggle-wrap label:before {
  background: #f0f0f0;
  content: "";
  position: absolute;
  width: 55px;
  height: 30px;
  border: 1px solid #e8eae9;
  left: -40px;
  top: 0;
  border-radius: 20px;
  transition: all 0.4s ease;
}
.wprf-toggle-wrap label:after {
  content: "";
  position: absolute;
  left: -38px;
  top: 2px;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background: #fbfbfb;
  transition: left 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), padding 0.3s ease, margin 0.3s ease;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 0 rgba(0, 0, 0, 0.08);
}