.wprf-input-radio-set-wrap {
  display: flex;
  flex-wrap: wrap;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set {
  height: 80px;
  width: 20%;
  position: relative;
  margin: 10px 10px 10px;
  box-shadow: 0 0 25px 0 rgba(0, 9, 78, 0.08);
}
.wprf-input-radio-set-wrap .wprf-input-radio-set input[type=radio] {
  display: none;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set input[type=radio]:checked + .wprf-input-radio-label {
  background-color: #0ccf9f;
  color: #fff;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set input[type=radio]:checked + .wprf-input-radio-label.with-wprf-radio-card-image {
  background-color: transparent;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set input[type=radio]:checked + .wprf-input-radio-label:after {
  content: "";
  display: inline-block;
  transform: rotate(45deg);
  height: 6px;
  width: 3px;
  border-bottom: 3.5px solid #fff;
  border-right: 3.5px solid #fff;
  position: absolute;
  right: -3px;
  top: -6px;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set input[type=radio]:checked + .wprf-input-radio-label:before {
  content: "";
  position: absolute;
  right: -10px;
  top: -10px;
  left: auto;
  width: 20px;
  height: 20px;
  border: 0;
  box-sizing: border-box;
  transition: all 0.3s ease 0s;
  background: #09dca8;
  border-radius: 50%;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set .wprf-input-radio-label {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease 0s;
  background: #fff;
  font-size: 14px;
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box;
}
.wprf-input-radio-set-wrap .wprf-input-radio-set .wprf-input-radio-label .wprf-radio-card-image {
  max-width: 100%;
}